main {
    max-width: 100vw;
    width: 100%;
    min-height: 100vh;
    max-height: 100vh;
    display: flex;
    flex-flow: row;
    width: 100%;
    justify-content: space-around;
    align-items: center;
}

section {
    flex: 1;
}

ol {
    list-style-type: none;
    display: flex;
    flex-flow: row;
    width: 100%;
    max-width: 500px;
    padding: 0;
    justify-content: space-around;
    align-items: center;
    margin: 0 auto 20px;
    
    & li {
        flex: 0 0 auto;
    }
}

.bulb {
    background-color: #888;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    padding: 10px;
    display: block;
    text-align: center;
    box-shadow: 0 2px 0 0 rgba(0, 0, 0, 0.5);
    position: relative;
